<template>
  <my-layout>
    <el-form slot="form" :inline="true" size="mini" ref="searchForm" :model="searchForm" class="search_form">
      <el-form-item label="教师名" prop="teacherName">
        <el-input placeholder="请输入教师名" clearable v-model="searchForm.teacherName" @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="username">
        <el-input placeholder="请输入用户名" clearable v-model="searchForm.username" @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="教师号" prop="teacherId">
        <el-input placeholder="请输入教师号" clearable v-model="searchForm.teacherId" @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="phone">
        <el-input placeholder="请输入手机号" clearable v-model="searchForm.phone" @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item slot="default">
        <el-button type="primary" @click="search">查询</el-button>
        <el-button type="primary" @click="resetForm('searchForm')">重置</el-button>
      </el-form-item>
    </el-form>
    <template slot-scope="scope" slot="tableLayout">
      <my-table @getData="getTable" ref="table" :tableData="tableData" :height="scope.height" :totalCount="totalCount">
        <template slot="tableHead">
          <el-table-column type="index" :index="computeIndex" label="序号"></el-table-column>
          <el-table-column prop="teacherName" label="教师名"></el-table-column>
          <el-table-column prop="username" label="用户名"></el-table-column>
          <el-table-column prop="teacherId" label="教师号"></el-table-column>
          <el-table-column prop="phone" label="手机号"></el-table-column>
          <el-table-column width="150" fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" plain @click="updateTeacher(scope.row)">修改</el-button>
            </template>
          </el-table-column>
        </template>
      </my-table>
    </template>
  </my-layout>
</template>

<script>
  import updateTeacherBox from "./updateTeacherBox";

  export default {
    name: 'allTeachers',
    data() {
      return {
        searchForm:{
          teacherName:'',
          username:'',
          teacherId:'',
          phone:''
        },
        ruleFormIndex: 'ruleForm' + Math.ceil(Math.random() * 100),
        tableData: [],
        totalCount: 0,
        selectedCourseId: '',
        page: 1,
        count: 10,
        tableObj:{}
      }
    },
    mounted() {
      this.tableObj = this.$refs['table']
    },
    methods: {
      getTable(page, count) {
        this.page = page
        this.count = count
        this.getTableData.call(this, '/admins/allTeachers', this.searchForm)
      },
      updateTeacher(row) {
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h(updateTeacherBox, {
            ref: this.ruleFormIndex,
            props: {teacherName: row.teacherName, phone: row.phone}
          }),
          showCancelButton: true,
          showConfirmButton: true,
          beforeClose: (action, instance, done) => {
            let ruleForm = this.$refs[this.ruleFormIndex]
            let form = ruleForm.ruleForm
            let formData
            if (action === 'confirm') {
              if (ruleForm.submitForm('ruleForm')) {
                instance.confirmButtonLoading = true;
                instance.confirmButtonText = '修改中...';
                formData = ruleForm.$data.ruleForm
                this.loading()
                this.axios.post('/admins/updateTeacher', {
                  teacherName: formData.teacherName,
                  phone: formData.phone,
                  teacherId: row.teacherId
                }).then(r => {
                  let data = r.data
                  this.loaded && this.loaded.close()
                  done();
                  instance.confirmButtonLoading = false;
                  for (let k in form) {
                    form[k] = ''
                  }
                  if (data.status === 0) {
                    this.$message.success(data.message)
                    this.tableObj.refresh()
                  } else {
                    this.$message.error(data.message)
                  }
                }, err => {
                  this.$err(err)
                  done();
                  instance.confirmButtonLoading = false;
                })
              }
            } else {
              done();
            }
            ruleForm.resetForm('ruleForm')
          }
        })
      },
      computeIndex(index) {
        return (this.page - 1) * this.count + index + 1
      },
      // 重置
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      search() {
        this.tableObj.search()
      }
    },
  }
</script>
